
export const Generate = () =>  {
    console.log("Generating Social Cards...")
    document.getElementById("images").classList.remove("hide");

    // Now allowing downloads
    const dl = document.querySelector('#allowDl');
    dl.classList.remove("disabled");
    
    // document.querySelector("#images").style.display = "";

    // User Inputs
    // Check Spanish?
    const spanish = document.getElementById("TLMD");

    var prop = document.getElementById("prop").value.trim().replace(/ +(?= )/g,'');
        if (prop == null || prop === "") {
            prop = "10"
        }
    var proposition = "PROPOSITION "

    var name = document.getElementById("name").value.trim().replace(/ +(?= )/g,'');
        if (name == null || name === "") {
            if (spanish.checked) { // Spanish Check
                name = "Iniciativa para el control del alquiler"
            }
            else {
                name = "Rent Control Initiative"
            }
        }
    var status = document.getElementById("StatusSelect").value;

    // Square Canvas
    const canvas = document.getElementById('igCanvas');
    const context = canvas.getContext('2d');

    // 16:9 Canvas
    const tCanvas = document.getElementById('twitterCanvas');
    const tContext = tCanvas.getContext('2d');

    // Story Canvas
    const sCanvas = document.getElementById('storyCanvas');
    const sContext = sCanvas.getContext('2d');

    // Loading Images
    // This draws images in order rather than multiple onloads all running at once
    // Ex: If the overlay loads before the image, they will stack out of order.
    // This is not an issue for the prop generator becuase it's only using 1 image per canvas however
    var env = process.env.PUBLIC_URL
    var sources = [env+"/images/sq-prop-card.png", env+"/images/tw-prop-card.png", env+"/images/story-prop-card.png"]
        // Spanish Check - Updating Variables if Spanish is Checked
        if (spanish.checked) {
            sources = [env+"/es-images/sq-prop-card.png", env+"/es-images/tw-prop-card.png", env+"/es-images/story-prop-card.png"]
            proposition = "PROPUESTA "
            let element = document.getElementById("StatusSelect");
            status = element.options[element.selectedIndex].getAttribute("data-es")
        }
    var imgCount = 0;
    const images = [];
   
    Object.entries(sources).forEach(element => {
        const img = new Image();
        img.src = element[1];
        images.push(img); // push to empty list
        imgCount ++; // add 1 to count
        img.onload = () => {
            imgCount --; // subtract 1 from count
            if (!imgCount) { 
                // Generate Square
                // drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                context.drawImage(images[0], 0, 0, 1800, 1800, 0, 0, 1000, 1000)
                squareText()

                // Generate 16:9
                tContext.drawImage(images[1], 0, 0, 1200, 675, 0, 0, 1200, 675)
                twitterText()

                // Generate Story
                sContext.drawImage(images[2], 0, 0, 540, 960, 0, 0, 540, 960)
                storyText()
            }
        }
    })

    // Wrap Text Function
    function wrapText(context, text, x, y, maxWidth, lineHeight) {
        var words = text.split(' ');
        var line = '';

        for(var n = 0; n < words.length; n++) {
          var testLine = line + words[n] + ' ';
          var metrics = context.measureText(testLine);
          var testWidth = metrics.width;
          if (testWidth > maxWidth && n > 0) {
            context.fillText(line, x, y);
            line = words[n] + ' ';
            y += lineHeight;
          }
          else {
            line = testLine;
          }
        }
        context.fillText(line, x, y);
      }

    // Square Overlay Text
    function squareText() {
        let adj = 0 // Quickly adjust all text up or down

        if (name.length >= 46) { // Long - 3 Lines
            adj = -50 
        }
        
        // Prop
        context.fillStyle = "#000000";
        context.textAlign = "center";
        context.font = "bold 70pt Flama Bold";
        context.fillText(proposition + prop, canvas.width/2, 230 + adj);
        
        // Name
        context.fillStyle = "#666666";
        context.textAlign = "center";
        context.font = "45pt Flama Light";
        if (name.length >= 46) { // Long - 3 Lines
            // wrapText(context, text, x, y, maxWidth, lineHeight);
            wrapText(context, name.toUpperCase(), canvas.width / 2, 290 + adj, canvas.width - 80, 50);
            adj = 100
        }
        if ((name.length >= 25 & name.length < 46)) { // Medium - 2 Lines
            wrapText(context, name.toUpperCase(), canvas.width / 2, 290 + adj, canvas.width - 50, 50);
            adj = 50
        }
        if (name.length < 25) { // Short - 1 Line
            context.fillText(name.toUpperCase(), canvas.width/2, 290 + adj);
        }

        // Status
        context.fillStyle = "#336633";
        context.textAlign = "center";
        context.font = "bold 70pt Flama Bold";
        if (name.length >= 46) { // Long - 3 Lines; canvas.width - 50
            // wrapText(context, text, x, y, maxWidth, lineHeight);
            wrapText(context, status.toUpperCase(), canvas.width / 2, 330 + adj, 305, 80);
        }
        if (name.length < 46) { // Everything Else
            wrapText(context, status.toUpperCase(), canvas.width / 2, 380 + adj, 305, 80);
        }
        
    };

    // 16:9 Overaly Text
    function twitterText() {
        let adj = 0

        // Prop
        tContext.fillStyle = "#000000";
        tContext.textAlign = "center";
        tContext.font = "bold 100pt Flama Bold";
        tContext.fillText(proposition + prop, tCanvas.width / 2, 280 + adj);

        // Name
        tContext.fillStyle = "#666666";
        tContext.textAlign = "center";
        tContext.font = "70pt Flama Light";
        if (name.length >= 36) { // Long - 2 Lines
            // wrapText(context, text, x, y, maxWidth, lineHeight);
            wrapText(tContext, name.toUpperCase(), tCanvas.width / 2, 370 + adj, 1100, 80);
            adj = 60 // Bumping the status text down to account for the text wrap
        }
        else { // Short - 1 Line
            tContext.fillText(name.toUpperCase(), tCanvas.width / 2, 380 + adj);
        }

        // Status
        tContext.fillStyle = "#336633";
        tContext.textAlign = "center";
        tContext.font = "bold 80pt Flama Bold";
        tContext.fillText(status.toUpperCase(), tCanvas.width / 2, 500 + adj);
    };

    // Story Overlay Text
    function storyText() {
        let adj = 0

        if (name.length >= 25) { // 2 - 3 Lines
            adj = -50
        }
        if (name.length >= 55) { // 4 Lines
            adj = -65
        }

        // Prop
        sContext.fillStyle = "#000000";
        sContext.textAlign = "center";
        sContext.font = "bold 60pt Flama Bold";
        sContext.fillText(proposition + prop, sCanvas.width / 2, 415 + adj);

        // Name
        sContext.fillStyle = "#666666";
        sContext.textAlign = "center";
        sContext.font = "45pt Flama Light";
        if (name.length >= 45) { // Long - 3 Lines
            // wrapText(context, text, x, y, maxWidth, lineHeight);
            wrapText(sContext, name.toUpperCase(), sCanvas.width / 2, 475 + adj, sCanvas.width - 50, 50);
            adj = 100
        }
        if ((name.length >= 25 & name.length < 45)) { // Medium - 2 Lines
            wrapText(sContext, name.toUpperCase(), sCanvas.width / 2, 475 + adj, sCanvas.width - 50, 50);
            adj = 50
        }
        if (name.length < 25) { // Short - 1 Line
            sContext.fillText(name.toUpperCase(), sCanvas.width / 2, 475 + adj);
        }

        // Status
        sContext.fillStyle = "#336633";
        sContext.textAlign = "center";
        sContext.font = "bold 70pt Flama Bold";
        if (name.length >= 45) { // Long - 3 Lines
            // wrapText(context, text, x, y, maxWidth, lineHeight);
            wrapText(sContext, status.toUpperCase(), 275, 550 + adj, 500, 80);
        }
        if ((name.length >= 25 & name.length < 45)) { // Medium - 2 Lines sCanvas.width / 2
            wrapText(sContext, status.toUpperCase(), 275, 550 + adj, 500, 80);
        }
        if (name.length < 25) { // Short - 1 Line
            wrapText(sContext, status.toUpperCase(), 275, 580 + adj, 500, 80);
        }
    };
    
}