// Libraries + Frameworks
import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col, Form, InputGroup, Button, ButtonGroup } from 'react-bootstrap';
import Crop from './cropper.tsx';
import { Generate } from "./generate";
import { Download } from "./download";

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <Container>
          <Form className="mb-3">

            <Row>
              <h1>NBC Proposition Social Card Generator</h1>
              <p>Details on the social cards can be changed after generating. Just change the details and click "Generate Cards" again.</p>

              {/* Local Toggle */}
              <Col xs={1}>
                  <Form.Check className='labels' id="TLMD" type="switch" label="Espaol?"/>
              </Col>
            </Row>

            <Row className='center'>
              {/* Proposition */}
              <Col lg={2}>
                <InputGroup className="mb-2">
                  <InputGroup.Text>Proposition</InputGroup.Text>
                  <Form.Control id="prop" aria-label="Proposition" type="number" placeholder='10'/>
                </InputGroup>
              </Col>

              {/* Name */}
              <Col lg={3}>
                <InputGroup className="mb-2">
                  <InputGroup.Text>Name</InputGroup.Text>
                  <Form.Control id="name" aria-label="Name" placeholder='Rent Control Initiative'/>
                </InputGroup>
              </Col>

              {/* Status */}
              <Col lg={3} className="mb-2">
                <InputGroup>
                  <InputGroup.Text>Status</InputGroup.Text>
                  <Form.Select id="StatusSelect" aria-label="Status" defaultValue={'Projected to Pass'}>
                  <option value="Projected to Pass" data-es="Proyeccin Aprobado">Projected to Pass</option>
                  <option value="Projected to Fail" data-es="Proyeccin Rechazado">Projected to Fail</option>
                    <option value="Passed" data-es="Aprobado">Passed</option>
                    <option value="Rejected" data-es="Rechazado">Rejected</option>
                    <option value="Tied" data-es="Empate">Tied</option>
                  </Form.Select>
                </InputGroup>
              </Col>

              {/* Buttons */}
              <Col lg={4} className="mb-3">
                <ButtonGroup className="pad w-100" aria-label="prop buttons">
                  <Button variant="outline-primary" type="button" id="submit" onClick={Generate}>Generate Cards</Button>
                  <Button className="disabled" variant="outline-primary" type="button" id="allowDl" onClick={Download}>Download</Button>
                </ButtonGroup>
              </Col>
            </Row>

          </Form>

          <Crop />
           
        </Container>
      </header>
    </div>
  );
}

export default App;